/* Style for input fields */
label {
  margin-right: 5px;
  font-weight: bold;
}

input {
  width: 100px;
  margin-right: 10px;
}

/* Style for export button */
button {
  margin-bottom: 10px;
  padding: 10px;
  font-weight: bold;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 50px;
  margin-top: 50px;
}

button:hover {
  background-color: #3e8e41;
  
}

/* Style for table */
table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 20px;
}

th, td {
  text-align: left;
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #ddd;
}
